

.card{
    background-color: #383838;
    border-radius: 10px;
    padding: 0px 20px 20px 20px;
    transition:  0.5s;
}
.info{
    display: flex;
    gap: 20px;
    padding: 20px 10px;
}
.description{
    display: flex;
    flex-direction: column;
    gap: 15px;
    padding-top: 10px;
}
#prev, #next{
    color: #cccccc;
    background: none;
    border: none;
    font-size: xxx-large;
    font-family: monospace;
    font-weight: bold;
    opacity: 0.5;
    transition: opacity 0.5s;
}
#prev:hover,
#next:hover{
    opacity: 1;
    color: #eeeeee;
}
